.panel {
  display: flex;
  position: absolute;
  width: 100%;
  bottom: 100px;
  justify-content: center;
}

@media only screen and (max-width: 600px) {
  .panel {
    bottom: 25px;
  }
}

.panel button {
  padding: 20px;
  font-size: 1.1rem;
  border: none;
  border-radius: 10px;
  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  min-width: 75px;
  min-height: 75px;
  margin: 0 10px;
}

.panel button:hover {
  filter: brightness(0.8);
}

.green {
  background-color: greenyellow;
}

.red {
  background-color: red;
}
