.backdrop {
  position: absolute;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.modal {
  position: absolute;
  z-index: 20;
  left: 0;
  top: 200px;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

.card {
  padding: 25px;
  background-color: azure;
  border-radius: 15px;
}

.card input {
  margin-top: 15px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid gray;
  padding: 5px;
}

.card button {
  padding: 15px;
  border-radius: 10px;
  cursor: pointer;
  border: none;
  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.25);
}

.card button:hover {
  filter: brightness(0.8);
}

.card menu {
  display: flex;
  gap: 25px;
  padding: 0;
  justify-content: center;
}

.red {
  background-color: red;
}

.green {
  background-color: greenyellow;
}
